:root {
    --bg-color: #f5f5f5;
    --text-color: #333;
    --input-border: #ccc;
    --input-bg: #fff;
    --add-btn-bg: #3498db;
    --add-btn-hover: #2980b9;
    --dark-bg-color: #2c3e50;
    --dark-text-color: #ecf0f1;
    --dark-input-bg: #34495e;
  }
  
  body {
    margin: 0;
    font-family: 'Arial', sans-serif;
  }
  
  .todo-app {
    background-color: var(--bg-color);
    color: var(--text-color);
    min-height: 100vh;
    padding: 20px;
    transition: background-color 0.3s ease;
  }
  
  .dark-mode {
    background-color: var(--dark-bg-color);
    color: var(--dark-text-color);
  }
  
  .dark-mode .todo-item,
  .dark-mode .todo-item input[type="text"],
  .dark-mode input[type="text"],
  .dark-mode select {
    background-color: var(--dark-input-bg);
    color: var(--dark-text-color);
  }
  
  .todo-header h1 {
    font-size: 36px;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .controls-container {
    display: flex;
    justify-content: center;
    gap: 10px;
  }
  
  input[type="text"] {
    padding: 8px;
    border-radius: 5px;
    border: 1px solid var(--input-border);
  }
  
  select {
    padding: 8px;
    border: none;
    border-radius: 4px;
    background-color: var(--input-bg);
    cursor: pointer;
  }
  
  .theme-toggle-btn {
    padding: 8px;
    border: none;
    background-color: #3498db;
    color: white;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .theme-toggle-btn:hover {
    background-color: var(--add-btn-hover);
  }
  
  .todo-list {
    margin-top: 20px;
  }
  
  .todo-item {
    display: flex;
    align-items: center;
    background-color: var(--input-bg);
    padding: 10px;
    border-radius: 5px;
    margin-bottom: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease;
  }
  
  .todo-item.completed p {
    text-decoration: line-through;
    color: gray;
  }
  
  .add-task-section {
    display: flex;
    margin-top: 20px;
    gap: 10px;
  }
  
  .new-task-input {
    flex: 1;
    padding: 8px;
    border: 1px solid var(--input-border);
    border-radius: 5px;
  }
  
  .add-task-btn {
    background-color: #2980b9;
    color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .add-task-btn:hover {
    background-color: #2980b9;
  }
  
  .add-btn {
    background-color: var(--add-btn-bg);
    color: white;
    border: none;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    font-size: 30px;
    cursor: pointer;
    position: fixed;
    bottom: 20px;
    right: 20px;
  }
  
  .add-btn:hover {
    background-color: var(--add-btn-hover);
  }
  
  .action-buttons {
    display: flex;
    gap: 10px;
    margin-left: auto;
  }
  
  .edit-btn,
  .delete-btn {
    background-color: transparent;
    border: none;
    cursor: pointer;
    font-size: 20px;
    padding: 5px;
  }
  
  
  .save-btn {
    background-color: #2980b9;
    color: white;
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .save-btn:hover {
    background-color: #2980b9;
  }
  